.navbar_main_container {
    height: 10.5vh;
    width: 100vw;
    background-color: #FFFFFF;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 12vh 0 12vh;
    position: relative;
}

.navbar_logo_container {
    cursor: pointer;
}

.navbar_links_container {
    font-weight: 600;
    display: flex;
    align-items: center;
}

.navbar_marketplace {
    position: relative;
    display: inline-block;
}

.marketplace_dropdown {
    position: absolute;
    background-color: #fff;
    top: 200%;
    border-top: 1px solid black;
    width: 10vw;
    z-index: 2;
    box-shadow: 0 0 10px 1px rgb(196, 195, 195);
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

li {
    padding: 8px 12px;
    width: 100%;
}
  
li:hover {
    background-color: rgba(151, 151, 151, 0.14);
    cursor: pointer;
}

span {
    font-size: 1.3vh;
    margin: 0 1vh 0 1vh;
}

.navbar_links_container > * {
    margin: 0 0 0 8vh;
    cursor: pointer;
}

.navbar_button {
    display: flex;
}

.navbar_button > * {
    margin: 0 0 0 7vh;
    height: 5.4vh;
    width: 8vw;
    border-radius: 1vh;
    font-size: 2vh;
    font-weight: 600;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navbar_login {
    background: none;
    border: 1px solid black;
}

.navbar_login:hover {
    background-color: rgb(241, 239, 239);
}

.navbar_register {
    background-color: #0F1011;
    color: #fff;
    position: relative;
}

.register_dropdown {
    border-top: 1px solid black;
    position: absolute;
    background-color: #fff;
    top: 130%;
    z-index: 2;
    color: #000;
    width: 10vw;
}

.navbar_hamburger {
    display: none;
}

.navbar_hamburger {
    position: relative;
}

.navbar_menu {
    position: absolute;
    top: 100%;
    z-index: 1000;
    width: 100vw;
    background-color: #000;
    color: #fff;
    transition: 1s;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-top-left-radius: 2vh;
    border-bottom-left-radius: 2vh;
}

.navbar_menu > div {
    height: 5%;
    font-size: 3vh;
    cursor: pointer;
    margin: 2vh;
}

.login_form_container {
    position: absolute;
    top: 100%;
    left: 0;    
    height: 100vh;
    z-index: 1000;
    width: 100vw;
    background-color: #000;
    color: #fff;
    transition: 1s;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-top-left-radius: 2vh;
    border-bottom-left-radius: 2vh;
}

.login_form_container .login_form {
    z-index: 1000;
    /* height: 70%; */
    width: 90%;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 3vh;
    border-radius: 1vh;
    color: #000;
}

.login_form_name { 
    height: 5%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.login_form_name h2 {
    margin: 0;
}

.login_form_name > div {
    font-size: 3vh;
    cursor: pointer;
}

