@media (max-width: 1240px) 
{ 
    .navbar_links_container {
        display: none;
    }

    .navbar_hamburger {
        height: 100%;
        width: 5vh;
        object-fit: contain;
        display: flex;
        align-items: center;
        cursor: pointer;
    }

    .navbar_hamburger img {
        width: 100%;
        height: auto;
    }

    .navbar_menu {
        
    }
}




@media (max-width: 1240px){
    .navbar_main_container {
        padding: 0 2vw;
    }
}